import axios from "axios";

const API_URL = "https://infnet-bootcamp-api.herokuapp.com/api/user"

const state = {
    users:[]
  };
  
  const getters = {
    allUsers: state => state.users,
    usuarioById: (state) => (id) => (state.users = state.users.filter(u => u._id == id))[0]
  };

  const actions = {
    getUsers({ commit }) {
        axios.get(
          API_URL
          ).then((response) => {
            commit('getUsers', response.data);
        });
    },
    addUser({ commit }, data){
      axios.post(
        API_URL, data
      ).then((response) => {
        commit("addUser", response.data);
    });
      
    },
    updateUser({ commit }, u){
      axios.patch(
        API_URL+ u._id, u
      ).then((response) => {
        commit("updateUser", response.data);
    });
      
    },
    deleteUser({ commit }, u) {
      axios.delete(
        API_URL + "/" +u.email
      ).then(() => {
        commit("removeUser", u._id);
    });
    },
    
  };
  const mutations = {
    getUsers: (state, users) => (state.users = users),
    removeUser: (state, id) => (state.users = state.users.filter(u => u._id !== id)),
    addUser: (state, todo) => state.users.push(todo),
    updateUser: (state, updUser) => {
      const index = state.users.findIndex(u => u._id === updUser._id)
      if (index !== -1) {
        state.users.splice(index, 1, updUser);
        }
      }
  };
 
  export default {
    state,
    getters,
    actions,
    mutations,
  };
  