<template>

  <div>
    <AddTodo/>
    <h3>Todos</h3>
    <div class="todos">
      <div
        v-for="todo in allTodos"
        :key="todo.id"
        :id="todo.id"
        class="todo"
        :class="{'is-complete': todo.completed}"
      >
      <router-link
            :to="{ name: 'todos-detail', params: { id: todo.id } }"
          > {{ todo.title }}
          </router-link>
       
      
        <b-icon @click="deleteTodo(todo.id)" variant="white" icon="trash" font-scale="1"  v-if="todo.completed == true"></b-icon>
         <b-iconstack font-scale="1" variant="white"  @click="completeTask(todo)" v-if="todo.completed == false">
          <b-icon stacked icon="square"></b-icon>
          <b-icon stacked icon="check"></b-icon>
        </b-iconstack>
       
      </div>
    </div>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import  AddTodo  from "./AddTodo.vue"
export default {
  name: "todos",
  components:{
    AddTodo
  },
  methods: {
    ...mapActions(["getTodos", "deleteTodo", "updateTodo"]),
    completeTask(todo){
      todo.completed= true
      this.updateTodo(todo)
    }
  },
  computed: mapGetters(["allTodos"]),
  created() {
    this.getTodos();
  }
};
</script>

<style>
h3{
  margin: 24px;
}
.todos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.todo {
  border: 1px solid #ccc;
  background: #41b883;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
  position: relative;
  cursor: pointer;
}
.b-icon {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  color: #fff;
}
.legend {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1rem;
}
a{
  color: white;
}
.complete-box {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #35495e;
}
.incomplete-box {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #41b883;
}
.is-complete {
  background: #35495e;
  color: #fff;
}
@media (max-width: 500px) {
  .todos {
    grid-template-columns: 1fr;
  }
}
</style>
