import axios from "axios";

const state = {
  todos: [],
  todo : null
};
const getters = {
  allTodos: state => state.todos,
  getTodo: state => state.todo,
  todoById: (state) => (id) => (state.todos.filter(t => t.id == id))[0],
};
const actions = {
  fetchTodos({ commit }) {
      commit("setTodos");
  },
  getTodos({ commit }) {
    axios.get(
        "https://jsonplaceholder.typicode.com/todos/"
      ).then((response) => {
        commit('getTodos', response.data);
    });
},
getTodoById({ commit }, id) {
  axios.get(
      "https://jsonplaceholder.typicode.com/todos/" + id
    ).then((response) => {
      commit('getTodo', response.data);
  }).catch(function (error) {
    commit('getTodo', error);
  });
},
   addTodo({ commit }, todo_add) {
    const new_index = Math.max(...state.todos.map(item => item.id)) + 1
    const response =  {
            "userId": 1,
            "id": new_index ,
            "title": todo_add.title,
            "completed": false,
          }
    commit("newTodo",response);
  },
  deleteTodo({ commit }, id) {
    commit("removeTodo", id);
  },
  updateTodo({ commit }, updTodo) {
    commit("updateTodo", updTodo);
  },
  
};
const mutations = {
  setTodos: (state) => state.todos,
  getTodos: (state, todo) => (state.todos = todo),
  getTodo: (state, data) => (state.todo = data),
  newTodo: (state, todo) => state.todos.push(todo),
  removeTodo: (state, id) =>
    (state.todos = state.todos.filter(t => t.id !== id)),
  updateTodo: (state, updTodo) => {
      const index = state.todos.findIndex(t => t.id === updTodo.id)
      if (index !== -1) {
        state.todos.splice(index, 1, updTodo);
        }
      }
};

export default {
  state,
  actions,
  getters,
  mutations
};
