<template>
  <div>
    <b-form inline>
    <label class="sr-only" for="inline-form-input-name">Tarefa</label>
    <b-input  v-model="todo_add.title"  id="inline-form-input-name" placeholder="Tarefa"
    ></b-input>
  <b-button class="save-btn" @click="onSubmit()">Save</b-button>
  </b-form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "AddTodo",
  data() {
    return {
      todo_add :{
        title: ""
      }
    };
  },
  methods: {
    ...mapActions(["addTodo"]),
    onSubmit() {
      this.addTodo(this.todo_add);
    }
  }
}
</script>

