<template>
  <div class="list">
    <AddUser/>
    <h1>Lista Usu치rios</h1>
    <b-row>
    <div class="cards">
        <b-card v-for="usuario in allUsers" 
        :key="usuario._id"
        :title="usuario.nome.toUpperCase()"
      
        tag="article"
        style="min-width: 30%;"
        class="mb-4 text-center"> 
          <router-link
            tag="p"
            :to="{ name: 'usersDetail', params: { id: usuario._id } }">
            <b-card-text>{{usuario.email}}</b-card-text>
            
        </router-link>
         <b-card-text>

            <b-icon variant="success" @click="inactiveUser(usuario)" icon="person" font-scale="1"  v-if="usuario.is_active == true" style="left: 10px"></b-icon>
            <b-icon variant="dark" @click="activeUser(usuario)" icon="person-fill" font-scale="1"  v-if="usuario.is_active == false" style="left: 10px"></b-icon>
            <b-icon @click="deleteUser(usuario)" variant="danger" icon="trash" style="left: 40px"></b-icon>
             </b-card-text>
            
        </b-card>
        </div>
    </b-row>
  </div>
</template>
<script>

import { mapGetters, mapActions } from "vuex";

import  AddUser  from "./AddUser.vue"

export default {
  name: 'ListaUsuario',
  components :{
    AddUser
  },
  methods: {
    ...mapActions(["getUsers", "deleteUser", "updateUser"]),
    inactiveUser(usuario){
      usuario.is_active = false
      this.updateUser(usuario)
    },
    activeUser(usuario){
      usuario.is_active = true
      this.updateUser(usuario)
    },
  },
  computed: mapGetters(["allUsers"]),
  created() {
    this.getUsers();
  }
}
</script>

<style scoped>

.cards {
  margin-top: 40px;
  display: flex;
  flex-wrap: inherit;
  align-items: flex-start;
  justify-content: space-between;
}
